import common from '../../common/index.js'
export default {
	data() {
		return {
			globalUser:{}
		}
	},
	onShow() {
		var me = this;
		me.globalUser = me.getGlobalUser();
	},
	
	methods: {
		clearCache(){
			// 清理缓存会删除所有数据,包括用户信息
			uni.clearStorageSync();
			uni.showToast({
				title:"清理缓存成功",
				duration:1500,
				mask:false
			});
		},
		logout(){
			var me = this;
			uni.request({
				url:common.serverURL + "/user/logout?&qq=843002185&userId=" + me.globalUser.id,
				method:"POST",
				success: (res) => {
					if(res.data.status == 200){
						// 退出登录成功
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url:"../me/me"
						});
					} else if(res.data.status == 500){
						uni.showToast({
							title:res.data.msg,
							mask:false,
							duration:1500
						});
					}
				}
			});
		},
		// 头像编辑
		operator(){
			var me = this;
			var globalUser = me.getGlobalUser()
			uni.showActionSheet({
				itemList:["查看我的头像", "从相册选择上传"],
				success: (res) => {
					var index = res.tapIndex;
					if (index == 0) {
						// 查看我的头像
						var faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls:faceArr,
							current:faceArr[0]
						});
					} else {
						//从相册选择上传
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType:["album"],
							success: (res) => {
								// 获得临时路径
								var tempFilePath = res.tempFilePaths[0];
								
								uni.navigateTo({
									url:"../meface/meface?tempFilePath=" + tempFilePath
								});
							}
						});
					}
				}
			});
		}
	}
}
